import Vue from 'vue'
import VueMixinsPack from '../packages'
import {isFunction, isObject, isString} from '../packages/utils/types'
import forceInterval from '../packages/utils/force-interval'

VueMixinsPack.use({
  name: 'domEvent',
  init(VueMixinsPack) {
    if (!window.resizeHandler) {
      window.resizeHandler = forceInterval(() => VueMixinsPack.dispatcher.$emit('resize', {type: 'window'}), 200)
      window.addEventListener('resize', window.resizeHandler)
    }
    if (!window.scrollHandler) {
      window.scrollHandler = () => VueMixinsPack.dispatcher.$emit('scroll') //, {top: $('body,html').scrollTop(), left: $('body,html').scrollLeft()}
      window.addEventListener('scroll', window.scrollHandler)
    }
  }
})
VueMixinsPack.store.set('data', {
  data() {
    return {loading: false}
  },
  methods: {
    isNull(str) {
      return str === null || str === '' || str === undefined || str.length <= 0
    },
    mergeValid(target, ...source) {
      source.forEach(src => Object.entries(src).forEach(([k, v]) => {
        if (v !== '' && v !== null && v !== undefined) target[k] = v
      }))
      return target
    },
    mergeArray(...params) {
      let lastParam = params.pop()
      let arr = [], map = {}, getUniqueKey
      if (isFunction(lastParam)) getUniqueKey = lastParam
      else if (isString(lastParam)) getUniqueKey = item => item[lastParam]
      else throw 'mergeArray need prop name'
      params.forEach(target => {
        target.forEach(item => {
          let unique = getUniqueKey(item)
          if (!map[unique]) {
            map[unique] = true
            arr.push(item)
          }
        })
      })
      return arr
    },
    operate({action, success, error, finished, cancel, confirm, prompt, notifySuccessTitle, notifyErrorTitle, dangerouslyUseHTMLString = false, loading = true}) {
      let actionFunc = (params) => {
        if (loading) this.loading = true
        action(params).then((data) => {
          if (data.success) {
            let {result, message} = data
            if (success) success(result, data)
            if (notifySuccessTitle) this.$notify({title: notifySuccessTitle, dangerouslyUseHTMLString, message, type: 'success'})
          } else {
            // throw data
            if (error) error(data)
            if (notifyErrorTitle) this.$notify({title: notifyErrorTitle, dangerouslyUseHTMLString, message: data.message, type: 'error'})
          }
          return data
        }).catch(e => {
          if (e.response || e.request || e.message === 'Network Error') {
            if (e.message === 'Network Error') e.message = '接口请求失败,数据未正常返回'
            if (error) error({success: false, message: e.toString()})
            if (notifyErrorTitle) this.$notify({title: notifyErrorTitle, dangerouslyUseHTMLString, message: e.toString(), type: 'error'})
          } else console.error(e)//本地环境时将输出前端错误
        }).finally(() => {
          if (loading) this.loading = false
          if (finished) finished()
        })
      }
      if (confirm) {
        let options = isObject(confirm) ? confirm : {message: confirm}
        if (!options.hasOwnProperty('title')) options.title = '提示'
        options.dangerouslyUseHTMLString = dangerouslyUseHTMLString
        this.$confirm(options.message, options).then(actionFunc).catch(cancel ? cancel : e => '')
      } else if (prompt) {
        this.$prompt(prompt.message, prompt).then((params) => actionFunc(params)).catch(cancel ? cancel : e => '')
      } else actionFunc()
    }
  }
})

VueMixinsPack.preset('page', 'default', 'data')

VueMixinsPack.plugin('service').add({
  name: 'demo',
  config: {
    baseURL: 'http://localhost:3000/api/test',
    timeout: 5000
  },
  init(axios) {
    axios.interceptors.request.use(config => config, error => Promise.reject(error))
    axios.interceptors.response.use(res => {
        let {config, status, data} = res
        if (res.data) {
          let {code, result} = res.data
          let success = code === 200
          if (config.pagination) {
            // result = result
          }
          return {success, result}
        }
        return res
      },
      error => Promise.reject(error)
    )
  },
  api: {
    list() {
      return {url: '/query', query: this.query, pagination: {page: 1, size: 10}}
    },
    create(form) {
      return {url: '/form', form}
    },
    update(data) {
      return {url: '/json', data}
    }
  }
})

Vue.use(VueMixinsPack)

export default VueMixinsPack
