export default {
  Number: {
    toModel(str) {
      return parseFloat(str)
    },
    toQueryString(num) {
      if (num !== 0 && !num) return NaN
      return num.toString()
    }
  },
  Boolean: {
    toModel(str) {
      return str !== '0' && str !== 'false'
    },
    toQueryString(bool) {
      if (!bool) return 'false'
      return bool.toString()
    }
  },
  Array: {
    toModel(str) {
      return str.split(',')
    },
    toQueryString(arr) {
      if (arr) return arr.join(',')
      return ''
    }
  },
  NumberArray: {
    toModel(str) {
      return str.split(',').map(s => parseFloat(s))
    },
    toQueryString(arr) {
      return arr.join(',')
    }
  }
}
