import {isFunction, isString} from '../../utils/types'

export default {
  name: 'globalEvent',
  init(VueMixinsPack) {
    VueMixinsPack.store.set('globalEvent', {
      data() {
        return {
          globalEventHandlers: {}
        }
      },
      mounted() {
        let eventHandlers = this.$options.eventHandlers
        if (eventHandlers) Object.entries(eventHandlers).forEach(([event, handler]) => {
          if (isString(handler)) handler = this[handler]
          if (isFunction(handler)) this.$VueMixinsPack.dispatcher.$on(event, this.globalEventHandlers[event] = handler.bind(this))
        })
      },
      beforeDestroy() {
        Object.entries(this.globalEventHandlers).forEach(([event, handler]) => this.$VueMixinsPack.dispatcher.$off(event, handler))
      }
    })
  }
}
