import Namespace from '../../utils/namespace'
import axios from 'axios'
import qs from 'qs'

const services = new Namespace()

class Service {
  constructor(service) {
    this.name = service.name
    this.axios = axios.create(service.config)
    service.init(this.axios)
    this.api = service.api
  }

  request(ins, api, ...args) {
    if (!this.api.hasOwnProperty(api)) throw `without api:${api} in service:${this.name} `
    let config = this.api[api].bind(ins)(...args)
    let {query, form, method} = config
    if (query) {
      config.params = query
      delete config.query
    }
    if (form) {
      config.data = qs.stringify(form)
      delete config.form
    }
    if (!method) config.method = config.data ? 'post' : 'get'
    return this.axios.request(config)
  }
}

export default {
  name: 'service',
  init(VueMixinsPack) {
    VueMixinsPack.store.set('service', {
      methods: {
        $api(api, ...args) {
          return () => this.$invoke(api, ...args)
        },
        $invoke(api, ...args) {
          let [service, uri] = api.split('/')
          return services.get(service).request(this, uri, ...args)
        }
      }
    })
  },
  add(service) {
    service = new Service(service)
    services.set(service.name, service)
  }
}
