<template>
  <div>I am listening window resize</div>
</template>

<script>
import VueMixinsPack from '../../packages'

export default {
  name: 'global-event-demo',
  mixins: VueMixinsPack.mixin('page'),
  eventHandlers: {
    resize({type}) {
      this.$message(`${type} resized`)
    }
  }
}
</script>

