<template>
  <el-row :gutter="20">
    <el-form label-position="top" class="clearfix">
      <el-col :span="10">
        <el-form-item label="localStorage.TestLocalData.PageData.testStr">
          <el-input v-model="PageData.testStr"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="10">
        <el-form-item label="localStorage.TestLocalData.PageData.testNum">
          <el-input-number v-model="PageData.testNum"></el-input-number>
        </el-form-item>
      </el-col>
      <el-col :span="4">
        <el-button type="primary" @click="localStorageUpdate">update</el-button>
      </el-col>
    </el-form>
  </el-row>
</template>

<script>
import VueMixinsPack from '../../packages'

export default {
  name: 'local-storage-demo',
  mixins: VueMixinsPack.mixin('page'),
  data() {
    let localData = this.$localStorage('TestLocalData', () => ({'PageData': {testStr: 'testStr', testNum: 123}}))
    return {
      localData,
      PageData: localData.get('PageData')
    }
  },
  methods: {
    localStorageUpdate() {
      this.localData.update()
    }
  }
}
</script>

