import Namespace from './utils/namespace'
import {readonly} from './utils/define'
import globalEvent from './plugins/global-event'
import query from './plugins/query'
import service from './plugins/service'
import localStorage from './plugins/local-storage'

const plugins = new Namespace()
const store = new Namespace()
const VueMixinsPack = {
  version: process.env.VERSION,
  store,
  use(plugin, ...options) {
    let pn = plugin.name
    if (!pn) throw `the plugin name is undefined`
    if (plugins.has(pn)) throw `the plugin name:${pn} is occupied`
    if (plugin.init) plugin.init(VueMixinsPack, ...options)
    plugins.set(pn, plugin)
  },
  install(Vue, ...options) {
    let dispatcher = options.shift()
    if (!dispatcher) dispatcher = new Vue()
    VueMixinsPack.dispatcher = dispatcher
    Object.defineProperty(Vue.prototype, '$VueMixinsPack', readonly(VueMixinsPack))
    Object.defineProperty(Vue, '$VueMixinsPack', readonly(VueMixinsPack))
    plugins.forEach(p => p.install && p.install(Vue, VueMixinsPack))
  },
  plugin(...name) {
    if (name.length === 1) return plugins.get(name[0])
    return plugins.map(...name)
  },
  preset(group, ...namespace) {
    store.group(group, ...namespace)
  },
  mixin(mixinGroup = 'default', mixins) {
    let list = store.list(mixinGroup)
    if (mixins) list.push(...mixins)
    return list
  }
}

VueMixinsPack.use(globalEvent)
VueMixinsPack.use(localStorage)
VueMixinsPack.use(service)
VueMixinsPack.use(query)
VueMixinsPack.preset('default', 'globalEvent', 'localStorage', 'service', 'query')

export default VueMixinsPack
