import Vue from 'vue'
import Router from 'vue-router'
import Main from './views/main'
import Query from './views/query'
import LocalStorage from './views/local-storage'
import GlobalEvent from './views/global-event'

const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}
Vue.use(Router)

let router = new Router({
  routes: [
    {name: 'main', path: '/', component: Main},
    {name: 'query', path: '/query-service', component: Query},
    {name: 'local-storage', path: '/local-storage', component: LocalStorage},
    {name: 'global-event', path: '/global-event', component: GlobalEvent}
  ]
})

router.beforeEach((to, from, next) => {
  next()
})

export default router
