import {isArray, isBoolean, isNumber, isString} from '../../utils/types'
import QueryConverter from '../../utils/query-converter'

export default {
  name: 'query',
  init(VueMixinsPack) {
    VueMixinsPack.store.set('query', {
      data() {
        return {
          queryConverter: {},
          queryDefault: {},
          query: {}
        }
      },
      watch: {
        '$route': '_handleRouteChange'
      },
      mounted() {
        this.$nextTick(() => {
          let conv = this.queryConverter
          Object.entries(this.query).forEach(([key, value]) => {
            if (!isString(value) && !conv.hasOwnProperty(key)) {
              if (isNumber(value)) conv[key] = QueryConverter.Number
              else if (isBoolean(value)) conv[key] = QueryConverter.Boolean
              else if (isArray(value)) conv[key] = QueryConverter.Array
            }
            if (isNumber(value) && isNaN(value)) value = this.query[key] = ''
            this.queryDefault[key] = value
          })
          this._handleRouteChange()
        })
      },
      methods: {
        $locate() {
          if (this.$options.$beforeLocate) this.$options.$beforeLocate.bind(this)(this.query, this.$route.params)
          let query = {}, conv = this.queryConverter
          Object.entries(this.query).forEach(([key, value]) => {
            if (conv.hasOwnProperty(key)) value = conv[key].toQueryString(value)
            if ((value || value === 0) && value !== '') query[key] = String(value).replace(/(^\s*)|(\s*$)/g, '')
          })
          this.$router.push({path: this.$route.path, query}).then(route => {
            // 参数未改变时也触发 _handleRouteChange，用于重复点击查询时能够强制刷新数据
            if (!route.path && !route.query) this._handleRouteChange()
          })
        },
        _handleRouteChange() {
          let query = {}, conv = this.queryConverter
          Object.entries(this.$route.query).forEach(([key, value]) => {
            value = value.replace(/(^\s*)|(\s*$)/g, '')
            if (conv.hasOwnProperty(key)) value = conv[key].toModel(value)
            query[key] = value
          })
          Object.assign(this.query, this.queryDefault, query)
          if (this.$options.$routeChanged) this.$options.$routeChanged.bind(this)(this.query, this.$route.params)
        }
      }
    })
  }
}
