import {isString} from '../../utils/types'

let support = false

try {
  window.localStorage.setItem('___test', 'OK')
  const result = window.localStorage.getItem('___test')
  window.localStorage.removeItem('___test')
  support = (result === 'OK')
} catch (err) {
  support = false
}

export default class LocalStorageItem {
  constructor(name, getDefValue) {
    if (!getDefValue) getDefValue = () => ({})
    this.__default__ = getDefValue
    this.name = name
    this.reload()
  }

  reload() {
    let locStr
    if (support) locStr = window.localStorage.getItem(this.name)
    this.__cache__ = !!locStr && isString(locStr) ? JSON.parse(locStr) : this.__default__(support)
    return this
  }

  set(key, value, flush = false) {
    this.__cache__[key] = value
    if (flush) this.update()
  }

  has(key) {
    return this.__cache__.hasOwnProperty(key)
  }

  get(key, getDefValue) {
    if (!this.__cache__.hasOwnProperty(key) && getDefValue) this.__cache__[key] = getDefValue()
    return this.__cache__[key]
  }

  del(key, flush = false) {
    delete this.__cache__[key]
    if (flush) this.update()
  }

  destroy() {
    this.__cache__ = {}
    if (support) window.localStorage.removeItem(this.name)
  }

  update() {
    if (support) window.localStorage.setItem(this.name, JSON.stringify(this.__cache__))
  }

  merge(obj, overwrite = true, flush = true) {
    Object.keys(obj).filter(key => overwrite || !this.has(key)).forEach(key => this.__cache__[key] = obj[key])
    if (flush) this.update()
  }
}
