<template>
  <el-form label-position="top" class="clearfix">
    <el-row :gutter="20">
      <el-col :span="5">
        <el-form-item label="query.num">
          <el-input-number v-model="query.num"></el-input-number>
        </el-form-item>
      </el-col>
      <el-col :span="5">
        <el-form-item label="query.str">
          <el-input v-model="query.str"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="5">
        <el-form-item label="query.bool">
          <el-switch v-model="query.bool"></el-switch>
        </el-form-item>
      </el-col>
      <el-col :span="5">
        <el-form-item label="query.arr">
          <el-select v-model="query.arr" multiple>
            <el-option v-for="(t,i) in query.arr" :key="i" :label="t" :value="t"></el-option>
          </el-select>
        </el-form-item>
      </el-col>
      <el-col :span="4">
        <el-button type="primary" @click="$locate">$locate</el-button>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="20">
        <el-form-item label="service response">
          <el-input type="textarea" :value="response" readonly :rows="22"></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="4">
        <div>
          <el-button type="primary" @click="request('list')">service-query</el-button>
        </div>
        <div style="margin:10px 0;">
          <el-button type="primary" @click="request('create')">service-form</el-button>
        </div>
        <div>
          <el-button type="primary" @click="request('update')">service-json</el-button>
        </div>
      </el-col>
    </el-row>
  </el-form>
</template>

<script>
import VueMixinsPack from '../../packages'

export default {
  name: 'query-demo',
  mixins: VueMixinsPack.mixin('page'),
  data() {
    return {
      query: {num: NaN, str: '', bool: false, arr: [0, 'p', 't', 1, 'o', 'n', 5]},
      response: ''
    }
  },
  mounted() {
    // console.log('this.isNull', this.isNull)
    // console.log('this.mergeValid', this.mergeValid)
    // console.log('this.mergeArray', this.mergeArray)
  },
  // $beforeLocate(query, params) {
  // },
  $routeChanged(query, params) {
    this.operate({
      action: this.$api('demo/list'),
      notifyErrorTitle: 'error',
      success: result => {
        this.response = JSON.stringify(result, null, 4)
      },
      error: res => {
        this.response = JSON.stringify(res, null, 4)
      }
    })
  },
  methods: {
    request(api) {
      this.$invoke(`demo/${api}`, this.query)
        .then(result => {
          this.response = JSON.stringify(result, null, 4)
        })
        .catch(e => {
          this.response = JSON.stringify(e, null, 4)
        })
    }
  }
}
</script>

