export default class Namespace {
  constructor() {
    this._namespace = {}
    this._groups = {}
  }

  set(name, value) {
    this._namespace[name] = value
  }

  has(name) {
    return this._namespace.hasOwnProperty(name)
  }

  get(name) {
    if (this.has(name)) return this._namespace[name]
    else console.warn(`the name:${name} is undefined`)
  }

  group(gName, ...name) {
    let g = this._groups
    if (name.length > 0) {
      if (this.has(gName)) throw `the group name:${gName} is occupied`
      g[gName] = name
    } else if (g.hasOwnProperty(gName)) {
      let ns = []
      g[gName].forEach(n => {
        if (this._groups.hasOwnProperty(n)) ns.push(...this.group(n))
        else ns.push(n)
      })
      return Array.from(new Set(ns))
    } else return []
  }

  forEach(cb) {
    Object.entries(this._namespace).forEach(([name, value]) => cb(value, name))
  }

  retrieve(names, cb) {
    names.forEach(name => {
      if (this._groups.hasOwnProperty(name)) this.group(name).map(name => cb(this.get(name), name))
      else if (this.has(name)) cb(this.get(name), name)
    })
  }

  list(...name) {
    let ns = []
    this.retrieve(name, value => ns.push(value))
    return ns
  }

  map(...name) {
    let ns = {}
    this.retrieve(name, (value, name) => ns[name] = value)
    return ns
  }

  delete(name) {
    if (this._groups.hasOwnProperty(name)) delete this._groups[name]
    else if (this.has(name)) delete this._namespace[name]
  }
}
