import Namespace from '../../utils/namespace'
import {readonly} from '../../utils/define'
import LocalStorageItem from './local-storage-item'

let storageHandler

const localStorageItems = new Namespace()
const item = function (name, getDefValue) {
  if (!localStorageItems.has(name)) localStorageItems.set(name, new LocalStorageItem(name, getDefValue))
  return localStorageItems.get(name)
}
const items = function (...name) {
  if (name.length === 1) return localStorageItems.get(name[0])
  return localStorageItems.map(...name)
}

export default {
  name: 'localStorage',
  item, items,
  init(VueMixinsPack) {
    if (!storageHandler) {
      storageHandler = (e) => {
        let key = e.key
        let item = this.item(key)
        if (item) VueMixinsPack.dispatcher.$emit('localStorage:' + key, item.reload())
      }
      window.addEventListener('storage', storageHandler)
    }
    VueMixinsPack.store.set('localStorage', {
      methods: {
        $localStorage: item,
        $localStorages: items
      }
    })
  },
  install(Vue) {
    Object.defineProperty(Vue, '$localStorage', readonly(item))
    Object.defineProperty(Vue, '$localStorages', readonly(items))
  }
}
